import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router';
import styled from '@emotion/styled';

import AuthServiceWrapper from '../components/AuthServiceWrapper';
import InputWithLabel from '../components/InputWithLabel';
import Button from '../components/Button';

import { api } from '../utils/api';

const Password = () => {
  const history = useHistory();
  const [info, setInfo] = useState({ password: '', rePassword: '' });
  const [error, setError] = useState({
    password: false,
    rePassword: false,
  });

  const handleChange = (e: any) => {
    e.persist();
    setInfo((prevState) => ({ ...prevState, [e.target.name]: e.target.value }));
  };

  const handleSubmit = async () => {
    const { data } = await api.post('/password/change', {
      password: info.password,
    });
    console.log(data);
    history.push('/');
  };

  useEffect(() => {
    if (info.rePassword && info.password !== info.rePassword) {
      setError((prevState) => ({ ...prevState, rePassword: true }));
    } else {
      setError((prevState) => ({ ...prevState, rePassword: false }));
    }
  }, [info.password, info.rePassword]);

  useEffect(() => {
    if (info.password.length < 8) {
      setError((prevState) => ({ ...prevState, password: true }));
    } else {
      setError((prevState) => ({ ...prevState, password: false }));
    }
  }, [info.password]);

  return (
    <>
      <AuthServiceWrapper
        title="새로운 비밀번호 설정"
        info={
          <>
            등록해주신 인증번호가 승인되었습니다.
            <br />
            새로운 비밀번호를 설정해주세요.
          </>
        }
      >
        <InputWrap>
          <InputWithLabel
            name="password"
            label="새로운 비밀번호"
            placeholder="8자리 이상의 비밀번호를 입력해주세요."
            type="password"
            onChange={handleChange}
            error={error.password}
            errorMessage="8자리 이상으로 입력"
          />
          <InputWithLabel
            name="rePassword"
            label="새로운 비밀번호 확인"
            placeholder="8자리 이상의 비밀번호를 입력해주세요."
            type="password"
            onChange={handleChange}
            error={error.rePassword}
            errorMessage="비밀번호 불일치"
          />
        </InputWrap>
        <ButtonWrap>
          <Button styleType="blue" onClick={handleSubmit}>
            비밀번호 변경 완료
          </Button>
          <Button styleType="outline" onClick={() => history.goBack()}>
            뒤로
          </Button>
        </ButtonWrap>
      </AuthServiceWrapper>
    </>
  );
};

const InputWrap = styled.div`
  & > * {
    margin-bottom: 20px;
  }
`;

const ButtonWrap = styled.div`
  margin-top: 50px;

  & > * {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }
`;

export default Password;
